import { androidOptions } from './android';
import { iosOptions } from './ios';
import { webOptions } from './web';

export const options = options => {
  console.log('after option');
  return {
    ...androidOptions(options),
    ...iosOptions(options),
    ...webOptions(options)
  };
};

export const legacyOptions = options => {
  const {priority = 'high', timeToLive = 60 * 60 * 24} = options;
  return {
    priority,
    timeToLive
  };
};
